.Container { 
  /* TODO: make this container actually match */
  background-color: #a37a74;
  height: 76px;
  width: 100%;
  /* TO DO: make this fit the text */
  min-width: 300px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 60px;

  font-size: 28px;
  font-weight: 700;
  /* TO DO: check the contrast later */
  color: #2f2f2f;
  overflow: hidden;
  line-height: 30px;

  position: 'fixed';
  top: 0;
  width: '100%';
  z-index: 1;
}

.Text {
  font-size: 26px;
  font-weight: 700;

  box-shadow: inset 0 0 0 0 #fff9fb;
  color: #2f2f2f;
  margin: 0 -.25rem;
  padding: 0 .25rem;
  
  transition: color .3s ease-in-out, box-shadow .3s ease-in-out;
}
.Text:hover {
  box-shadow: inset 400px 0 0 0 #fff9fb;
  color: #2f2f2f;
  font-size: 27px;
}


.Text {
  display: flex;
  flex-direction: column;
  row-gap: 30px;
}

a {
  text-decoration: none;
  color: inherit;
}